import React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import Location from '../../mobx/Location'
import { useTranslate } from '../hooks/useTranslate'

interface Params {
	product: string
}

interface Product {
	retval: number
	retdesc: ''
	product: {
		id: number
		id_prev: number
		id_next: number
		name: string
		price: number
		currency: string
		url: string
		info: string
		add_info: string
		release_date: string
		agency_fee: number
		agency_sum: number
		agency_id: number
		collection: string
		propertygood: number
		is_available: number
		pwyw: number
		label: string
		no_cart: number
		prices: [
			{
				code: string
				price: number
				currency: string
			}
		]
		payment_methods: [
			{
				name: string
				currencies: [
					{
						currency: string
						code: string
					}
				]
			}
		]
		prices_unit: {
			unit_name: string
			unit_amount: number
			unit_amount_desc: string
			unit_currency: string
			unit_cnt: number
			unit_cnt_min: number
			unit_cnt_desc: string
		}
		type: string
		text: {
			date: string
			size: number
		}
		category_id: number
		breadcrumbs: [
			{
				id: number
				name: string
				products_cnt: number
			}
		]
		units: {
			desc: string
			price: number
			discounts: [
				{
					desc: string
					percent: number
					price: number
				}
			]
		}
		options: [
			{
				name: string
				label: string
				comment: string
				type: string
				required: number
			}
		]
		options_check: number
		statistics: {
			sales: number
			sales_hidden: number
			refunds: number
			refunds_hidden: number
			good_reviews: number
			good_reviews_hidden: number
			bad_reviews: number
			bad_reviews_hidden: number
		}
		seller: {
			id: number
			name: string
		}
	}
}

const ProductComponent = observer(({ match }: RouteComponentProps<Params>) => {
	const [product, setProduct] = React.useState<Product>()
	const translate = useTranslate({
		loading: { en: 'Loading description', ru: 'Загрузка описания' },
		retdesc: { en: 'This product does not exist', ru: 'Такого товара не существует' },
	})

	React.useEffect(() => {
		fetch(`https://api.digiseller.ru/api/products/${match.params.product}/info?lang=${Location.getLanguage()}`, {
			headers: {
				Accept: 'application/json',
			},
		})
			.then(response => response.json())
			.then(response => {
				setProduct({ ...response })
				console.log(response)
			})
	}, [])

	if (!product) return <>{translate.loading}...</>
	if (product.retdesc) return <>{translate.retdesc}.</>

	return <pre style={{ width: '100%', overflow: 'hidden' }}>{JSON.stringify(product, null, 4)}</pre>
})

export default ProductComponent
