import { makeAutoObservable } from 'mobx'

const EN = 'en-US'
const RU = 'ru-RU'

class Location {
	private country_code?: 'en-US' | 'ru-RU'

	constructor() {
		makeAutoObservable(this)
	}

	async setCountryCode() {
		if (!this.country_code) {
			await fetch('https://api.2ip.ua/geo.json')
				.then(response => response.json())
				.then(response => {
					if (response.country_code === 'UA' || response.country_code === 'RU' || response.country_code === 'BY') {
						this.country_code = RU
					} else {
						this.country_code = EN
					}
				})
		}
	}

	getLanguage() {
		return this.country_code || EN
	}
}

export default new Location()
